import React, { useState } from 'react';

const principlesData = [
  {
    title: 'Avoid Borrowing for Major Expenses',
    description:
      'Borrowing for major expenses, such as a home or car, leads to significant interest costs over time. Instead, focus on saving and paying in cash whenever possible.',
    details: [
      'Borrowing increases overall costs due to interest payments.',
      'Cash payments prevent long-term debt commitments.',
      'Avoid impulse purchases driven by access to credit.',
    ],
  },
  {
    title: 'Live Within Your Means',
    description:
      'Ensure your expenses are lower than your income to build a sustainable financial future.',
    details: [
      'Create and stick to a realistic monthly budget.',
      'Identify non-essential spending and cut back.',
      'Build an emergency fund to avoid debt in unexpected situations.',
    ],
  },
  {
    title: 'Build a Cash-First Savings Plan',
    description:
      'Prioritize saving cash to cover all expected and unexpected expenses.',
    details: [
      'Set short- and long-term savings goals.',
      'Automate savings contributions for consistency.',
      'Use high-yield savings accounts for optimal growth.',
    ],
  },
  {
    title: 'Reject Traditional Banking Systems',
    description:
      'Explore alternatives like decentralized finance (DeFi) to maximize financial independence.',
    details: [
      'Reduce reliance on high-fee traditional banking systems.',
      'Take control of your financial transactions with DeFi tools.',
      'Educate yourself on secure, decentralized platforms.',
    ],
  },
];

const Principles = () => {
  const [activePrinciple, setActivePrinciple] = useState(null);

  const toggleDetails = (index) => {
    setActivePrinciple(activePrinciple === index ? null : index);
  };

  return (
    <section id="principles" className="section">
      <h2>Core Principles of the Cash-First Financial Strategy</h2>
      <p>
        These principles serve as the foundation for building financial
        freedom and security. Click on each principle to learn more about how
        it can transform your financial strategy.
      </p>

      <div className="principles-container">
        {principlesData.map((principle, index) => (
          <div key={index} className="principle">
            <h3 onClick={() => toggleDetails(index)}>
              {principle.title}
              <span
                style={{
                  marginLeft: '8px',
                  cursor: 'pointer',
                  color: '#008080',
                }}
              >
                {activePrinciple === index ? '-' : '+'}
              </span>
            </h3>
            <p>{principle.description}</p>
            {activePrinciple === index && (
              <ul>
                {principle.details.map((detail, idx) => (
                  <li key={idx}>{detail}</li>
                ))}
              </ul>
            )}
          </div>
        ))}
      </div>
    </section>
  );
};

export default Principles;