import React, { useState } from 'react';
import {
  Chart as ChartJS,
  LineElement,
  PointElement,
  LinearScale,
  CategoryScale,
  Tooltip,
  Legend,
} from 'chart.js';
import { Line } from 'react-chartjs-2';

ChartJS.register(CategoryScale, LinearScale, LineElement, PointElement, Tooltip, Legend);

const Roadmap = () => {
  const [goal, setGoal] = useState('');
  const [goalAmount, setGoalAmount] = useState('');
  const [timeframe, setTimeframe] = useState(12); // Default: 12 months
  const [monthlyContribution, setMonthlyContribution] = useState('');
  const [milestones, setMilestones] = useState([]);
  const [progressData, setProgressData] = useState(null);
  const [feedback, setFeedback] = useState('');

  const calculateRoadmap = () => {
    const parsedGoalAmount = parseFloat(goalAmount);
    const parsedMonthlyContribution = parseFloat(monthlyContribution);

    if (!goal || isNaN(parsedGoalAmount) || isNaN(parsedMonthlyContribution) || parsedGoalAmount <= 0 || parsedMonthlyContribution <= 0) {
      setFeedback('Please enter a valid goal, amount, and monthly contribution.');
      return;
    }

    const totalSavings = parsedMonthlyContribution * timeframe;
    const isGoalAchievable = totalSavings >= parsedGoalAmount;

    // Generate milestones
    const milestoneCount = 5; // Number of milestones
    const milestoneInterval = Math.floor(timeframe / milestoneCount);
    const milestoneSavings = (parsedGoalAmount / milestoneCount).toFixed(2);

    const milestoneSteps = Array.from({ length: milestoneCount }, (_, i) => ({
      month: (i + 1) * milestoneInterval,
      savings: milestoneSavings * (i + 1),
    }));

    setMilestones(milestoneSteps);

    // Generate chart data
    const savingsData = Array.from({ length: timeframe }, (_, i) => parsedMonthlyContribution * (i + 1));
    setProgressData({
      labels: Array.from({ length: timeframe }, (_, i) => `Month ${i + 1}`),
      datasets: [
        {
          label: 'Savings Progress',
          data: savingsData,
          borderColor: '#008080',
          backgroundColor: 'rgba(0, 128, 128, 0.1)',
          fill: true,
        },
        {
          label: 'Goal Amount',
          data: Array(timeframe).fill(parsedGoalAmount),
          borderColor: '#ff5733',
          borderDash: [10, 5],
          borderWidth: 2,
        },
      ],
    });

    // Feedback
    setFeedback(
      isGoalAchievable
        ? 'Your roadmap is achievable within the selected timeframe!'
        : 'Consider increasing your monthly contribution or extending your timeframe to meet your goal.'
    );
  };

  return (
    <section id="roadmap" className="section">
      <h2>Smart Financial Roadmap</h2>
      <p>
        Create a roadmap to achieve your financial goals. Input your goal, amount, timeframe, and monthly contribution to get started.
      </p>

      {/* Input Section */}
      <div>
        <label htmlFor="goal">Financial Goal:</label>
        <input
          id="goal"
          type="text"
          placeholder="E.g., Buy a Home, Start a Business"
          value={goal}
          onChange={(e) => setGoal(e.target.value)}
        />

        <label htmlFor="goalAmount">Goal Amount ($):</label>
        <input
          id="goalAmount"
          type="number"
          placeholder="Enter the total amount you need"
          value={goalAmount}
          onChange={(e) => setGoalAmount(e.target.value)}
        />

        <label htmlFor="timeframe">
          Timeframe (Months): <strong>{timeframe} months</strong>
        </label>
        <input
          id="timeframe"
          type="range"
          min="6"
          max="60"
          step="1"
          value={timeframe}
          onChange={(e) => setTimeframe(Number(e.target.value))}
        />

        <label htmlFor="monthlyContribution">Monthly Contribution ($):</label>
        <input
          id="monthlyContribution"
          type="number"
          placeholder="Enter your monthly contribution"
          value={monthlyContribution}
          onChange={(e) => setMonthlyContribution(e.target.value)}
        />
      </div>

      {/* Generate Button */}
      <button onClick={calculateRoadmap} style={buttonStyle}>
        Generate Roadmap
      </button>

      {/* Feedback */}
      {feedback && <p style={{ marginTop: '1rem', color: feedback.includes('achievable') ? 'green' : 'red' }}>{feedback}</p>}

      {/* Milestone Steps */}
      {milestones.length > 0 && (
        <div style={{ marginTop: '2rem' }}>
          <h3>Your Milestones</h3>
          <ul>
            {milestones.map((milestone, index) => (
              <li key={index}>
                By Month {milestone.month}: Save ${milestone.savings.toLocaleString()}
              </li>
            ))}
          </ul>
        </div>
      )}

      {/* Progress Visualization */}
      {progressData && (
        <div style={{ marginTop: '2rem' }}>
          <h3>Progress Visualization</h3>
          <Line
            data={progressData}
            options={{
              responsive: true,
              plugins: {
                legend: { display: true, position: 'bottom' },
                title: { display: true, text: 'Savings vs. Goal Over Time' },
              },
              scales: {
                y: {
                  beginAtZero: true,
                  title: { display: true, text: 'Amount in USD' },
                },
                x: {
                  title: { display: true, text: 'Time (Months)' },
                },
              },
            }}
          />
        </div>
      )}
    </section>
  );
};

// Button Styling
const buttonStyle = {
  marginTop: '1rem',
  padding: '0.75rem 1.5rem',
  backgroundColor: '#008080',
  color: 'white',
  border: 'none',
  borderRadius: '4px',
  cursor: 'pointer',
  fontWeight: 'bold',
};

export default Roadmap;