import React, { useState, useEffect } from 'react';

const Header = () => {
  const [activeSection, setActiveSection] = useState('');
  const [isScrolled, setIsScrolled] = useState(false);
  const [menuOpen, setMenuOpen] = useState(false);

  // Highlight active section while scrolling
  useEffect(() => {
    const handleScroll = () => {
      const sections = document.querySelectorAll('section');
      const scrollPosition = window.scrollY + 200; // Offset for better detection

      sections.forEach((section) => {
        if (
          scrollPosition >= section.offsetTop &&
          scrollPosition < section.offsetTop + section.offsetHeight
        ) {
          setActiveSection(section.id);
        }
      });

      // Change header style on scroll
      setIsScrolled(window.scrollY > 50);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  // Toggle menu for mobile view
  const toggleMenu = () => {
    setMenuOpen((prev) => !prev);
  };

  return (
    <header className={`header ${isScrolled ? 'scrolled' : ''}`}>
      <div className="header-content">
        <h1>Cash-First Financial Strategy</h1>
        <button
          className="menu-toggle"
          onClick={toggleMenu}
          aria-label={menuOpen ? 'Close menu' : 'Open menu'}
        >
          
        </button>
      </div>
      <nav className={`nav ${menuOpen ? 'open' : ''}`}>
        <ul>
          <li>
            <a
              href="#overview"
              className={activeSection === 'overview' ? 'active' : ''}
              onClick={() => setMenuOpen(false)}
            >
              Overview
            </a>
          </li>
          <li>
            <a
              href="#principles"
              className={activeSection === 'principles' ? 'active' : ''}
              onClick={() => setMenuOpen(false)}
            >
              Core Principles
            </a>
          </li>
          <li>
            <a
              href="#tools"
              className={activeSection === 'tools' ? 'active' : ''}
              onClick={() => setMenuOpen(false)}
            >
              Interactive Tools
            </a>
          </li>
          <li>
            <a
              href="#defi"
              className={activeSection === 'defi' ? 'active' : ''}
              onClick={() => setMenuOpen(false)}
            >
              DeFi
            </a>
          </li>
          <li>
            <a
              href="#budget"
              className={activeSection === 'budget' ? 'active' : ''}
              onClick={() => setMenuOpen(false)}
            >
              Budgeting
            </a>
          </li>
          <li>
            <a
              href="#lending"
              className={activeSection === 'lending' ? 'active' : ''}
              onClick={() => setMenuOpen(false)}
            >
              Lending Circles
            </a>
          </li>
          <li>
            <a
              href="#roadmap"
              className={activeSection === 'roadmap' ? 'active' : ''}
              onClick={() => setMenuOpen(false)}
            >
              Roadmap
            </a>
          </li>
          <li>
            <a
              href="#community"
              className={activeSection === 'community' ? 'active' : ''}
              onClick={() => setMenuOpen(false)}
            >
              Community
            </a>
          </li>
          <li>
            <a
              href="#savings-tracker"
              className={activeSection === 'savings-tracker' ? 'active' : ''}
              onClick={() => setMenuOpen(false)}
            >
              Savings Tracker
            </a>
          </li>
        </ul>
      </nav>
    </header>
  );
};

export default Header;