import React, { useState } from 'react';
import {
  Chart as ChartJS,
  ArcElement,
  Tooltip,
  Legend,
} from 'chart.js';
import { Doughnut } from 'react-chartjs-2';

ChartJS.register(ArcElement, Tooltip, Legend);

const Budget = () => {
  const [income, setIncome] = useState('');
  const [incomeType, setIncomeType] = useState('monthly'); // Default input type
  const [needsPercentage, setNeedsPercentage] = useState(50);
  const [wantsPercentage, setWantsPercentage] = useState(30);
  const [savingsPercentage, setSavingsPercentage] = useState(20);
  const [results, setResults] = useState(null);
  const [error, setError] = useState('');

  const calculateMonthlyIncome = () => {
    const parsedIncome = parseFloat(income);
    if (isNaN(parsedIncome) || parsedIncome <= 0) {
      return 0; // Invalid income
    }
    switch (incomeType) {
      case 'hourly':
        return parsedIncome * 40 * 4.33; // 40 hours/week, 4.33 weeks/month
      case 'weekly':
        return parsedIncome * 4.33; // Weekly to monthly
      case 'annual':
        return parsedIncome / 12; // Annual to monthly
      default:
        return parsedIncome; // Monthly income directly
    }
  };

  const calculateBudget = () => {
    const monthlyIncome = calculateMonthlyIncome();

    if (!monthlyIncome) {
      setError('Please enter a valid income.');
      return;
    }

    const totalPercentage = needsPercentage + wantsPercentage + savingsPercentage;

    if (totalPercentage !== 100) {
      setError('The total percentage must equal 100%. Adjust your budget allocation.');
      return;
    }

    setError(''); // Clear previous errors

    const needs = (monthlyIncome * (needsPercentage / 100)).toFixed(2);
    const wants = (monthlyIncome * (wantsPercentage / 100)).toFixed(2);
    const savings = (monthlyIncome * (savingsPercentage / 100)).toFixed(2);

    setResults({
      needs,
      wants,
      savings,
      totalIncome: monthlyIncome.toFixed(2),
    });
  };

  return (
    <section id="budget" className="section">
      <h2>Ultimate Cash-First Budgeting</h2>
      <p>
        Plan your budget effectively by allocating your income into Needs,
        Wants, and Savings. Select your income type, adjust percentages, and
        visualize your financial plan.
      </p>

      {/* Income Input Section */}
      <div>
        <label htmlFor="incomeType">Income Type:</label>
        <select
          id="incomeType"
          value={incomeType}
          onChange={(e) => setIncomeType(e.target.value)}
        >
          <option value="hourly">Hourly Wage</option>
          <option value="weekly">Weekly Income</option>
          <option value="monthly">Monthly Income</option>
          <option value="annual">Annual Salary</option>
        </select>

        <label htmlFor="income">
          Enter Your {incomeType === 'hourly' ? 'Hourly Wage' : incomeType === 'weekly' ? 'Weekly Income' : incomeType === 'annual' ? 'Annual Salary' : 'Monthly Income'}:
        </label>
        <input
          id="income"
          type="number"
          placeholder={`Enter your ${incomeType} amount`}
          value={income}
          onChange={(e) => setIncome(e.target.value)}
        />
      </div>

      {/* Percentage Sliders */}
      <div style={{ marginTop: '1rem' }}>
        <label htmlFor="needsPercentage">Needs (%): {needsPercentage}%</label>
        <input
          id="needsPercentage"
          type="range"
          min="0"
          max="100"
          step="1"
          value={needsPercentage}
          onChange={(e) => setNeedsPercentage(Number(e.target.value))}
        />

        <label htmlFor="wantsPercentage">Wants (%): {wantsPercentage}%</label>
        <input
          id="wantsPercentage"
          type="range"
          min="0"
          max="100"
          step="1"
          value={wantsPercentage}
          onChange={(e) => setWantsPercentage(Number(e.target.value))}
        />

        <label htmlFor="savingsPercentage">Savings (%): {savingsPercentage}%</label>
        <input
          id="savingsPercentage"
          type="range"
          min="0"
          max="100"
          step="1"
          value={savingsPercentage}
          onChange={(e) => setSavingsPercentage(Number(e.target.value))}
        />
      </div>

      {/* Calculate Button */}
      <button onClick={calculateBudget} style={buttonStyle}>
        Calculate Budget
      </button>

      {/* Error Message */}
      {error && <p style={{ color: 'red' }}>{error}</p>}

      {/* Budget Results */}
      {results && (
        <div style={{ marginTop: '2rem' }}>
          <h3>Your Budget Breakdown</h3>
          <p>
            <strong>Total Monthly Income:</strong> ${results.totalIncome}
          </p>
          <p>
            <strong>Needs:</strong> ${results.needs} ({needsPercentage}%)
          </p>
          <p>
            <strong>Wants:</strong> ${results.wants} ({wantsPercentage}%)
          </p>
          <p>
            <strong>Savings:</strong> ${results.savings} ({savingsPercentage}%)
          </p>

          {/* Doughnut Chart Visualization */}
          <Doughnut
            data={{
              labels: ['Needs', 'Wants', 'Savings'],
              datasets: [
                {
                  data: [
                    parseFloat(results.needs),
                    parseFloat(results.wants),
                    parseFloat(results.savings),
                  ],
                  backgroundColor: ['#008080', '#ff5733', '#ffd700'],
                  hoverOffset: 4,
                },
              ],
            }}
            options={{
              responsive: true,
              plugins: {
                legend: {
                  display: true,
                  position: 'bottom',
                },
              },
            }}
          />
        </div>
      )}
    </section>
  );
};

// Button Styling
const buttonStyle = {
  marginTop: '1rem',
  padding: '0.75rem 1.5rem',
  backgroundColor: '#008080',
  color: 'white',
  border: 'none',
  borderRadius: '4px',
  cursor: 'pointer',
  fontWeight: 'bold',
};

export default Budget;