import React, { useState } from 'react';
import {
  Chart as ChartJS,
  BarElement,
  CategoryScale,
  LinearScale,
  Tooltip,
  Legend,
} from 'chart.js';
import { Bar } from 'react-chartjs-2';

ChartJS.register(CategoryScale, LinearScale, BarElement, Tooltip, Legend);

const Lending = () => {
  const [groupSize, setGroupSize] = useState(5); // Default number of members
  const [contribution, setContribution] = useState(100); // Default contribution per member
  const [duration, setDuration] = useState(12); // Default duration in months

  const calculateTotal = () => {
    const monthlyTotal = groupSize * contribution;
    const totalPooled = monthlyTotal * duration;
    return {
      monthlyTotal,
      totalPooled,
    };
  };

  const { monthlyTotal, totalPooled } = calculateTotal();

  return (
    <section id="lending" className="section">
      <h2>Community Lending Circles</h2>
      <p>
        Community lending circles are trust-based financial systems where members pool resources to help each other meet financial goals.
        Use the tool below to explore how pooling even small contributions can lead to significant capital over time.
      </p>

      {/* Input Section */}
      <div>
        <label htmlFor="groupSize">
          Number of Members: <strong>{groupSize}</strong>
        </label>
        <input
          id="groupSize"
          type="range"
          min="2"
          max="20"
          value={groupSize}
          onChange={(e) => setGroupSize(Number(e.target.value))}
        />

        <label htmlFor="contribution">
          Contribution per Member ($): <strong>${contribution}</strong>
        </label>
        <input
          id="contribution"
          type="range"
          min="10"
          max="1000"
          step="10"
          value={contribution}
          onChange={(e) => setContribution(Number(e.target.value))}
        />

        <label htmlFor="duration">
          Duration (Months): <strong>{duration} months</strong>
        </label>
        <input
          id="duration"
          type="range"
          min="1"
          max="24"
          value={duration}
          onChange={(e) => setDuration(Number(e.target.value))}
        />
      </div>

      {/* Results Section */}
      <div style={{ marginTop: '1rem' }}>
        <h3>Results</h3>
        <p>
          <strong>Monthly Pooled Total:</strong> ${monthlyTotal.toLocaleString()}
        </p>
        <p>
          <strong>Total Pooled Over {duration} Months:</strong> ${totalPooled.toLocaleString()}
        </p>
      </div>

      {/* Visualization */}
      <div style={{ marginTop: '2rem' }}>
        <h3>Capital Growth Visualization</h3>
        <Bar
          data={{
            labels: Array.from({ length: duration }, (_, i) => `Month ${i + 1}`),
            datasets: [
              {
                label: 'Pooled Amount',
                data: Array.from({ length: duration }, (_, i) => (i + 1) * monthlyTotal),
                backgroundColor: '#008080',
              },
            ],
          }}
          options={{
            responsive: true,
            plugins: {
              legend: { display: false },
              tooltip: {
                callbacks: {
                  label: (context) => `$${context.raw.toLocaleString()}`,
                },
              },
            },
            scales: {
              y: {
                beginAtZero: true,
                title: {
                  display: true,
                  text: 'Pooled Capital ($)',
                },
              },
              x: {
                title: {
                  display: true,
                  text: 'Time (Months)',
                },
              },
            },
          }}
        />
      </div>
    </section>
  );
};

export default Lending;