import React, { useState, useEffect } from 'react';
import ChartDataLabels from 'chartjs-plugin-datalabels';
import {
  Chart as ChartJS,
  BarElement,
  CategoryScale,
  LinearScale,
  Tooltip,
  Legend,
} from 'chart.js';
import { Bar } from 'react-chartjs-2';

// Register required components with Chart.js
ChartJS.register(CategoryScale, LinearScale, BarElement, Tooltip, Legend);
ChartJS.register(ChartDataLabels);

const Tools = () => {
  const [loanAmount, setLoanAmount] = useState(300000); // Default value
  const [interestRate, setInterestRate] = useState(3.5); // Default value
  const [loanTerm, setLoanTerm] = useState(30); // Default value
  const [results, setResults] = useState(null);
  const [savings, setSavings] = useState(null);

  // Utility functions for formatting
  const formatCurrency = (value) => `$${parseFloat(value).toLocaleString()}`;
  const formatPercentage = (value) => `${parseFloat(value).toFixed(2)}%`;

  const calculateMortgage = () => {
    const monthlyRate = interestRate / 100 / 12;
    const numberOfPayments = loanTerm * 12;

    const monthlyPayment =
      (loanAmount * monthlyRate) /
      (1 - Math.pow(1 + monthlyRate, -numberOfPayments));

    const totalPaid = monthlyPayment * numberOfPayments;
    const totalInterest = totalPaid - loanAmount;

    // Dynamic shorter-term calculation (rounded up to nearest year)
    const shorterTerm = Math.ceil(loanTerm / 2);
    const shorterTermPayments = shorterTerm * 12;
    const shorterMonthlyPayment =
      (loanAmount * monthlyRate) /
      (1 - Math.pow(1 + monthlyRate, -shorterTermPayments));
    const shorterTotalPaid = shorterMonthlyPayment * shorterTermPayments;
    const shorterTotalInterest = shorterTotalPaid - loanAmount;

    setResults({
      monthlyPayment: monthlyPayment.toFixed(2),
      totalPaid: totalPaid.toFixed(2),
      totalInterest: totalInterest.toFixed(2),
    });
    setSavings({
      shorterTerm,
      shorterMonthlyPayment: shorterMonthlyPayment.toFixed(2),
      shorterTotalPaid: shorterTotalPaid.toFixed(2),
      shorterTotalInterest: shorterTotalInterest.toFixed(2),
    });
  };

  useEffect(() => {
    calculateMortgage();
  }, [loanAmount, interestRate, loanTerm]);
  
  const loadScenario = (scenario) => {
    if (scenario === 'starter') {
      setLoanAmount(200000);
      setInterestRate(7.3);
      setLoanTerm(30);
    } else if (scenario === 'mid') {
      setLoanAmount(500000);
      setInterestRate(6.9);
      setLoanTerm(30);
    } else if (scenario === 'luxury') {
      setLoanAmount(800000);
      setInterestRate(6.5);
      setLoanTerm(30);
    }
  };

  return (
    <section id="tools" className="section">
      <h2>Mortgage Interest Visualizer</h2>
      <p>
        Use the sliders below to explore how different loan details impact your monthly payment, total interest paid, and remaining balance over time.
      </p>

      {/* Preconfigured Scenarios */}
      <div style={{ marginBottom: '1rem' }}>
        <button onClick={() => loadScenario('starter')} style={buttonStyle}>
          Starter Home
        </button>
        <button onClick={() => loadScenario('mid')} style={buttonStyle}>
          Mid-Level Home
        </button>
        <button onClick={() => loadScenario('luxury')} style={buttonStyle}>
          Luxury Home
        </button>
      </div>

      {/* Input Sliders */}
      <div>
        <label htmlFor="loanAmount">
          Loan Amount ($): <strong>{formatCurrency(loanAmount)}</strong>
        </label>
        <input
          id="loanAmount"
          type="range"
          min="50000"
          max="1000000"
          step="1000"
          value={loanAmount}
          onChange={(e) => setLoanAmount(Number(e.target.value))}
          aria-label="Loan amount slider"
        />

        <label htmlFor="interestRate">
          Annual Interest Rate (%): <strong>{interestRate.toFixed(2)}%</strong>
        </label>
        <input
          id="interestRate"
          type="range"
          min="1"
          max="10"
          step="0.1"
          value={interestRate}
          onChange={(e) => setInterestRate(Number(e.target.value))}
          aria-label="Interest rate slider"
        />

        <label htmlFor="loanTerm">
          Loan Term (Years): <strong>{loanTerm} years</strong>
        </label>
        <input
          id="loanTerm"
          type="range"
          min="5"
          max="30"
          step="1"
          value={loanTerm}
          onChange={(e) => setLoanTerm(Number(e.target.value))}
          aria-label="Loan term slider"
        />
      </div>

      {results && savings && (
        <div>
          <h3>Comparison of Terms</h3>
          <Bar
            data={{
              labels: [
                `Current Term (${loanTerm} Years)`,
                `Shorter Term (${savings.shorterTerm} Years)`,
                'Cash Purchase',
              ],
              datasets: [
                {
                  label: 'Principal',
                  data: [loanAmount, loanAmount, loanAmount],
                  backgroundColor: '#008080',
                  stack: 'Stack 0',
                },
                {
                  label: 'Interest',
                  data: [results.totalInterest, savings.shorterTotalInterest, 0],
                  backgroundColor: '#ff5733',
                  stack: 'Stack 0',
                },
              ],
            }}
            options={{
              responsive: true,
              plugins: {
                tooltip: {
                  callbacks: {
                    label: (context) =>
                      `${context.dataset.label}: ${formatCurrency(context.raw)}`,
                  },
                },
                legend: { display: true },
                title: {
                  display: true,
                  text: 'Total Amount Paid (Principal + Interest)',
                },
                datalabels: {
                  display: (context) => context.datasetIndex === 1, // Only display for the top (Interest) dataset
                  color: '#000',
                  font: {
                    size: 14,
                    weight: 'bold',
                  },
                  anchor: 'end', // Position label above the bar
                  align: 'end', // Aligns outside the top of the bar
                  formatter: (_, context) => {
                    const principal = Number(context.chart.data.datasets[0].data[context.dataIndex]); // Principal
                    const interest = Number(context.chart.data.datasets[1].data[context.dataIndex]); // Interest
                    return formatCurrency(principal + interest); // Display the total (Principal + Interest)
                  },
                },
              },
              scales: {
                y: {
                  beginAtZero: true,
                  title: { display: true, text: 'Amount in USD' },
                },
                x: {
                  title: {
                    display: true,
                    text: 'Loan Terms',
                  },
                },
              },
            }}
          />

          <h3>Details</h3>
          <div style={{ display: 'flex', justifyContent: 'space-between' }}>
            <div>
              <h4>Current Term ({loanTerm} Years)</h4>
              <p>Monthly Payment: {formatCurrency(results.monthlyPayment)}</p>
              <p>Total Interest: {formatCurrency(results.totalInterest)}</p>
              <p>Total Paid: {formatCurrency(results.totalPaid)}</p>
            </div>
            <div>
              <h4>Shorter Term ({savings.shorterTerm} Years)</h4>
              <p>Monthly Payment: {formatCurrency(savings.shorterMonthlyPayment)}</p>
              <p>Total Interest: {formatCurrency(savings.shorterTotalInterest)}</p>
              <p>Total Paid: {formatCurrency(savings.shorterTotalPaid)}</p>
            </div>
          </div>
        </div>
      )}
    </section>
  );
};

// Styling for buttons
const buttonStyle = {
  margin: '0.5rem',
  padding: '0.5rem 1rem',
  backgroundColor: '#008080',
  color: 'white',
  border: 'none',
  borderRadius: '4px',
  cursor: 'pointer',
};

export default Tools;