import React, { useState } from 'react';

const defiTopics = [
  {
    title: 'Decentralized Wallets',
    description:
      'Manage your crypto assets securely without relying on centralized services.',
    details: [
      'Examples: MetaMask, Trust Wallet, Ledger.',
      'Control your private keys and secure your assets.',
      'Interact with DeFi platforms directly through your wallet.',
    ],
  },
  {
    title: 'Decentralized Exchanges (DEX)',
    description:
      'Trade cryptocurrencies directly with peers without intermediaries.',
    details: [
      'Examples: Uniswap, SushiSwap, PancakeSwap.',
      'Lower fees compared to traditional exchanges.',
      'Maintain privacy and control over your funds.',
    ],
  },
  {
    title: 'Yield Farming',
    description:
      'Earn rewards by providing liquidity to decentralized platforms.',
    details: [
      'Stake your crypto assets in liquidity pools.',
      'Earn returns in the form of additional tokens.',
      'Examples: Aave, Compound, Yearn Finance.',
    ],
  },
  {
    title: 'Risks in DeFi',
    description:
      'Understand the risks before engaging with decentralized finance.',
    details: [
      'Market volatility: Crypto prices can change rapidly.',
      'Smart contract vulnerabilities: Bugs in code can be exploited.',
      'Impermanent loss: Value fluctuations in liquidity pools.',
    ],
  },
];

const Defi = () => {
  const [activeTopic, setActiveTopic] = useState(null);

  const toggleTopic = (index) => {
    setActiveTopic(activeTopic === index ? null : index);
  };

  return (
    <section id="defi" className="section">
      <h2>Decentralized Finance (DeFi)</h2>
      <p>
        DeFi offers innovative financial opportunities by leveraging blockchain
        technology. Click on each topic to learn more.
      </p>

      <div className="defi-container">
        {defiTopics.map((topic, index) => (
          <div key={index} className="defi-topic">
            <h3 onClick={() => toggleTopic(index)}>
              {topic.title}
              <span
                style={{
                  marginLeft: '8px',
                  cursor: 'pointer',
                  color: '#008080',
                }}
              >
                {activeTopic === index ? '-' : '+'}
              </span>
            </h3>
            <p>{topic.description}</p>
            {activeTopic === index && (
              <ul>
                {topic.details.map((detail, idx) => (
                  <li key={idx}>{detail}</li>
                ))}
              </ul>
            )}
          </div>
        ))}
      </div>
    </section>
  );
};

export default Defi;